<template>
	<v-container>
		<v-layout row wrap>
			<v-flex xs6>
				<!--b>Caso: </b>{{caso}} <br>
				<b>Fecha creación: </b>{{Datos_caso.Fecha_creación}} <br>
				<b>Fecha envío: </b>{{Datos_caso.Fecha_envío}} <br>
				<b>Fecha recepción: </b>{{Datos_caso.Fecha_recepción}} <br>
				<b>Fecha respuesta final: </b>{{Datos_caso.Fecha_respuesta_final}} <br>
				<b>Archivos adjuntos: </b>{{Datos_caso.Archivos_adjuntos}}<b-->
				<b>Caso: </b>{{caso}} <br>
				<b>Fecha creación: </b>{{this.datosCaso.fecha_creacion}}<br>
				<b>Fecha envío: </b>{{this.datosCaso.fecha_envio}} <br>
				<b>Fecha recepción: </b>{{this.datosCaso.fecha_recepcion}} <br>
				<b>Fecha respuesta final: </b>{{this.datosCaso.fecha_respuesta_final}} <br>
				<b>Archivos adjuntos: </b>caso.pdf
			</v-flex>
			<v-flex xs6>
				<embed :src="this.urlPDF" type="application/pdf" width="500px" height="500px" />
				<!--embed src='{{this.$store.state.host}}/api/archivos/42' type="application/pdf" width="500px" height="500px" /-->
					<!--embed src="caso.pdf" type="application/pdf" width="500px" height="500px" /-->
					<br>
				Vista Previa 
			</v-flex>
		</v-layout>
		<template>		
			<h3>Historial de actualizaciones</h3>
	  <v-data-table
		    :headers="headers"
		    :items="actualizaciones"
		    class="elevation-1"
		  ><template v-slot:items="props">
		      <td>{{ props.item.name }}</td>
		      <td>{{ props.item.fecha_modificacion }}</td>
		      <td>{{ props.item.comentario }}</td>
		      <td>{{ props.item.nombre_estado }}</td>
		      <!--td><a>{{ (props.item.archivos_adjuntos) }}</a></td-->
		      <td><a :href="urlDescAdj">{{ (props.item.archivos_adjuntos) }}</a></td>
		    </template>
		  </v-data-table>
		</template>
	</v-container>
</template>
<script>
	const axios=require('axios');
	export default{
		name:"HistorialCambios",
		data(){
			return{
				Components:{
				},
				urlPDF:'',
				//urlVer:this.$store.state.host+'/api/archivos/'+archivoprinc+'?api_token='+this.$store.state.api_token,
				comp:'Historial de cambios',
				headers: [
                  { 
                    text: 'Modificado por',
                    //align: 'left',
                    sortable: false,
                    value: 'Modificador', 
                  },
                  { text: 'Fecha modificación', value: 'fecha_modificacion' },
                  { text: 'Comentarios hechos', value: 'comentario' },
                  { text: 'Estado asigando', value: 'estado' },
                  { text: 'Archivos adjuntos', value: 'Archivos_adjuntos', sortable: false }
                ],
				/*Datos_caso: {
					id_caso:'id_caso1',
					Fecha_creación: '27-08-2019',
					Fecha_envío: '27-08-2019',
					Fecha_recepción: '28-08-2019',
					Fecha_respuesta_final:'29-08-2019',
					Archivos_adjuntos:'adjunto.pdf'
				},*/
				actualizaciones:[],
				datosCaso: [],
				caso: this.$route.params.id_caso,
				archivoprinc:0,
				archivos:[],
				urlDescAdj:'',
			}
		},
		mounted(){
			//let uri = `http://localhost:8000/api/actualizaciones?caso_id=${this.caso}`;
			//CREACION DE UN NUEVO REGISTRO EN LA BASE DE DATOS DE ACTUALIZACIONES Y ACTUALIZACION DEL ESTADO DEL CASO EN LA TABLA DE CASOS
			//let uri = `http://localhost:8000/api/actualizaciones/${this.caso}`;

			//console.log(this.urlPDF);

			//BUSQUEDA DE LAS ACTUALIZACIONES REALIZADAS A UN CASO
			//let uri = this.$store.state.host+'/api/actualizaciones/'+this.caso;
			let uri = this.$store.state.host+'/api/actualizaciones/'+this.caso+'?api_token='+this.$store.state.api_token;
			//let uri = this.$store.state.host+'/api/actualizaciones/'+this.caso;
            axios.get(uri).then((response) => {
                let res = response.data;
                if (res.status_code == 200) {
                    this.actualizaciones=res.actualizaciones;
                    this.archivoprinc=res.archivoprinc.id;
                    //this.archivos=split()
                    //console.log('id archivo principal del caso: '+res.archivoprinc.id);
                    this.urlPDF=this.$store.state.host+'/api/archivos/'+this.archivoprinc+'?api_token='+this.$store.state.api_token;
                    this.urlDescAdj=this.$store.state.host+'/api/archivos/'+this.archivoprinc+'/descargar?api_token='+this.$store.state.api_token;
                }
            })
            .catch(error => {
                if(error.response.status == 404) {
                    console.log("No se encontraron actualizaciones.");
                } else {
                    console.log("Hubo un problema con la búsqueda de actualizaciones.");
                }
            });

            //BUSQUEDA DE INFORMACION DEL CASO
            let uri2 = this.$store.state.host+'/api/casos/'+this.caso+'?api_token='+this.$store.state.api_token;
            axios.get(uri2)
            .then((response) => {
                let res2 = response.data;
                if (res2.status_code == 200) {
                	//console.log(res2.caso);
                    this.datosCaso=res2.caso;
                }
            })
            .catch(error => {
                if(error.response.status == 404) {
                    console.log("No se encontraron casos.");
                } else {
                    console.log("Hubo un problema con la búsqueda de casos.");
                }
            });
		}
	}
</script>